import React from "react";
import HoverBorderGradient from "./ui/hover-border-gradient";
import Imagen_About from "../img/About_me.jpg";

export default function About() {
  return (
    <div className="about__grid">
      <figure className="about__photo">
        <img src={Imagen_About} alt="imagen_about" className="imagen_about" />
      </figure>

      <div className="about__content">
        <h2 className="h1">Conozca al Dr. Orlando Santilln</h2>

        <p className="about__p">
          Soy el <span className="about__keyword">Dr. Orlando Santilln</span>, un{" "}
          <span className="about__keyword">cirujano plstico certificado</span> con{" "}
          <span className="about__keyword">ms de 15 aos de experiencia</span>, reconocido por mi{" "}
          <span className="about__keyword">enfoque artstico</span> y por mi compromiso con la{" "}
          <span className="about__keyword">seguridad</span> y la{" "}
          <span className="about__keyword">satisfaccin de mis pacientes</span>.
        </p>

        <p className="about__p">
          Graduado con honores de la{" "}
          <span className="about__keyword">Universidad Central del Ecuador</span> y con
          especializaciones en <span className="about__keyword">Brasil</span> y{" "}
          <span className="about__keyword">Estados Unidos</span>, combino{" "}
          <span className="about__keyword">tcnicas avanzadas</span> con un entendimiento profundo de la{" "}
          <span className="about__keyword">anatoma</span> y la{" "}
          <span className="about__keyword">esttica</span> para lograr resultados{" "}
          <span className="about__keyword">armnicos y naturales</span>.
        </p>

        <div className="about__cta">
          <HoverBorderGradient as="a" href="/#contacto" className="hbg--light">
            Solicitar consulta
          </HoverBorderGradient>
        </div>
      </div>
    </div>
  );
}
