// src/components/Hero.jsx
"use client";
import React, { useEffect, useRef } from "react";
import { motion } from "framer-motion";
import HoverBorderGradient from "./ui/hover-border-gradient";
import video from "../img/Video-explicativo.mp4";
import SplitText from "./SplitText";

export default function Hero() {
  const videoRef = useRef(null);

  // Variantes para animar botones y chips
  const groupVariants = {
    hidden: { opacity: 0, y: 24 },
    visible: (customDelay = 0) => ({
      opacity: 1,
      y: 0,
      transition: {
        delay: customDelay,
        duration: 0.5,
        ease: "easeOut",
        staggerChildren: 0.12,
      },
    }),
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 },
  };

  //  Animacin + lgica de reproduccin / audio segn visibilidad
  useEffect(() => {
    const el = videoRef.current;
    if (!el) return;

    // Animacin inicial
    const id = window.requestAnimationFrame(() => {
      el.classList.add("is-visible");
    });

    // Intentar empezar en silencio para cumplir polticas de autoplay
    el.muted = true;
    const playPromise = el.play?.();
    if (playPromise && playPromise.catch) {
      playPromise.catch(() => {});
    }

    let hasUserInteracted = false;
    let isVisible = false;

    //  Cuando el usuario hace click/touch por primera vez, habilitamos audio
    const unlockAudio = () => {
      hasUserInteracted = true;
      if (el && isVisible) {
        el.muted = false;
        const p = el.play?.();
        if (p && p.catch) p.catch(() => {});
      }
    };

    window.addEventListener("click", unlockAudio, { once: true });
    window.addEventListener("touchstart", unlockAudio, { once: true });

    //  Observamos si el video est visible en pantalla
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.target !== el) return;

          isVisible = entry.isIntersecting && entry.intersectionRatio > 0.3;

          if (isVisible) {
            const p = el.play?.();
            if (p && p.catch) p.catch(() => {});
            // si ya hubo interaccin del usuario, encendemos audio
            el.muted = !hasUserInteracted;
          } else {
            // fuera de vista: muteamos y pausamos
            el.muted = true;
            el.pause?.();
          }
        });
      },
      {
        threshold: [0, 0.3, 0.7],
      }
    );

    observer.observe(el);

    return () => {
      window.cancelAnimationFrame(id);
      observer.disconnect();
      window.removeEventListener("click", unlockAudio);
      window.removeEventListener("touchstart", unlockAudio);
    };
  }, []);

  //  Doble click para agrandar a pantalla completa
  const handleExpandHero = () => {
    const v = videoRef.current;
    if (!v) return;
    try {
      if (v.requestFullscreen) {
        const fsPromise = v.requestFullscreen();
        if (fsPromise && fsPromise.catch) {
          fsPromise.catch((err) => {
            console.warn("Fullscreen bloqueado por el navegador:", err);
          });
        }
      } else if (v.webkitEnterFullscreen) {
        // Safari iOS
        v.webkitEnterFullscreen();
      }
    } catch (e) {
      console.warn("No se pudo entrar en fullscreen", e);
    }
  };

  return (
    <section id="inicio" className="section hero">
      <div className="container hero__grid">
        {/* BLOQUE DE TEXTO */}
        <div className="hero__content">
          {/* TTULO ANIMADO */}
          <SplitText
            text="Ciruga plstica facial con precisin y criterio esttico"
            className="h1 hero__title"
            delay={80}
            duration={0.55}
            ease="power3.out"
            splitType="chars"
            from={{ opacity: 0, y: 40 }}
            to={{ opacity: 1, y: 0 }}
            threshold={0.2}
            rootMargin="-60px"
            textAlign="left"
          />

          {/* SUBTTULO ANIMADO */}
          <SplitText
            text="Ciencia + arte, trato humano y resultados naturales."
            className="h3 lead hero__sub"
            delay={40}
            duration={0.5}
            ease="power2.out"
            splitType="chars"
            from={{ opacity: 0, y: 20 }}
            to={{ opacity: 1, y: 0 }}
            threshold={0.2}
            rootMargin="-60px"
            textAlign="left"
          />

          {/* CTAS PRINCIPALES (ANIMADAS) */}
          <motion.div
            className="hero__actions"
            style={{ display: "flex", flexWrap: "wrap", gap: "1rem" }}
            variants={groupVariants}
            initial="hidden"
            animate="visible"
            custom={0.5}
          >
            <motion.div variants={itemVariants}>
              <HoverBorderGradient as="a" href="#contacto" className="hbg--slow">
                Agenda tu consulta
              </HoverBorderGradient>
            </motion.div>

            <motion.div variants={itemVariants}>
              <HoverBorderGradient as="a" href="#testimonios" className="hbg--light">
                Ver testimonios
              </HoverBorderGradient>
            </motion.div>
          </motion.div>

          {/* TTULO DE CHIPS  AHORA ENTRA JUNTO CON EL HERO, SIN DEPENDER DEL SCROLL */}
          <SplitText
            text="Procedimientos Destacados."
            className="h3 lead hero__sub"
            delay={40}
            duration={0.5}
            ease="power2.out"
            splitType="chars"
            from={{ opacity: 0, y: 20 }}
            to={{ opacity: 1, y: 0 }}
            threshold={0.2}
            rootMargin="-60px"
            textAlign="left"
          />

          {/* BOTONES DE PROCEDIMIENTOS (ANIMADOS) */}
          <motion.div
            className="hero__chips chips"
            variants={groupVariants}
            initial="hidden"
            animate="visible"
            custom={1.0}
          >
            <motion.a
              href="#/procedimiento/rinoplastia"
              className="proc-pill"
              variants={itemVariants}
            >
              Rinoplastia
            </motion.a>
            <motion.a
              href="#/procedimiento/aumento-de-mamas"
              className="proc-pill"
              variants={itemVariants}
            >
              Aumento de Mamas
            </motion.a>
            <motion.a
              href="#/procedimiento/liposuccion"
              className="proc-pill"
              variants={itemVariants}
            >
              Liposuccin
            </motion.a>
          </motion.div>
        </div>

        {/* VIDEO EXPLICATIVO */}
        <aside className="hero__media">
          <video
            loading="lazy"
            ref={videoRef}
            src={video}
            autoPlay
            loop
            muted          // arranca muteado para que el navegador lo deje reproducir
            playsInline
            className="hero__video"
            onDoubleClick={handleExpandHero}   // doble click = fullscreen
          />
        </aside>
      </div>
    </section>
  );
}
