// src/components/FinalSection.jsx
import React, { useRef, useEffect } from "react";
import { motion } from "framer-motion";
import video from "../img/Video_Final.mp4";
import HoverBorderGradient from "./ui/hover-border-gradient";

const FinalSection = () => {
  const videoRef = useRef(null);
  const isVisibleRef = useRef(false);
  const hasInteractedRef = useRef(false);

  //  Desbloquear audio despus del primer click/touch del usuario
  useEffect(() => {
    const unlockAudio = () => {
      hasInteractedRef.current = true;
      const v = videoRef.current;
      if (v && isVisibleRef.current) {
        v.muted = false;
        const p = v.play?.();
        if (p && p.catch) p.catch(() => {});
      }
    };

    window.addEventListener("click", unlockAudio, { once: true });
    window.addEventListener("touchstart", unlockAudio, { once: true });

    return () => {
      window.removeEventListener("click", unlockAudio);
      window.removeEventListener("touchstart", unlockAudio);
    };
  }, []);

  const handleEnter = () => {
    const v = videoRef.current;
    if (!v) return;
    isVisibleRef.current = true;

    // si el usuario ya interactu, se escucha el audio
    const shouldHaveAudio = hasInteractedRef.current;
    v.muted = !shouldHaveAudio;

    const p = v.play?.();
    if (p && p.catch) p.catch(() => {});
  };

  const handleLeave = () => {
    const v = videoRef.current;
    if (!v) return;
    isVisibleRef.current = false;
    // fuera de vista: muteamos y frenamos
    v.muted = true;
    v.pause?.();
    v.currentTime = 0;
  };

  //  Doble click para agrandar a pantalla completa (sin romper si el navegador lo bloquea)
  const handleExpandFinal = () => {
    const v = videoRef.current;
    if (!v) return;

    try {
      if (v.requestFullscreen && (document.fullscreenEnabled ?? true)) {
        const fsPromise = v.requestFullscreen();
        if (fsPromise && fsPromise.catch) {
          fsPromise.catch((err) => {
            console.warn("Fullscreen bloqueado por el navegador:", err);
          });
        }
      } else if (v.webkitEnterFullscreen) {
        // Safari iOS
        v.webkitEnterFullscreen();
      } else {
        // si no hay API de fullscreen soportada, simplemente no hacemos nada
        console.warn("Fullscreen no soportado en este navegador.");
      }
    } catch (e) {
      console.warn("No se pudo entrar en fullscreen", e);
    }
  };

  return (
    <section id="final-section" className="cloudy-bg final-section">
      {/* CAPA DE PARTCULAS */}
      <div className="final-section__particles" />

      <motion.div
        className="final-section__inner"
        initial={{ opacity: 0, y: 60 }}
        whileInView={{ opacity: 1, y: 0 }}
        viewport={{ once: true, amount: 0.35 }}
        transition={{ duration: 0.8, ease: "easeOut" }}
      >
        {/* VIDEO VERTICAL CENTRADO */}
        <motion.div
          className="final-section__video-vertical video-glow"
          initial={{ scale: 0.9, opacity: 0 }}
          whileInView={{ scale: 1, opacity: 1 }}
          viewport={{ once: false, amount: 0.5 }}
          transition={{ duration: 0.9, ease: "easeOut", delay: 0.15 }}
          whileHover={{ scale: 1.02 }}
          onViewportEnter={handleEnter}
          onViewportLeave={handleLeave}
        >
          <video
            loading="lazy"
            ref={videoRef}
            className="final-section__video-el-vertical"
            src={video}
            playsInline
            muted        // arranca muteado, luego se activa el audio tras interaccin
            controls
            onDoubleClick={handleExpandFinal}
          />
        </motion.div>

        {/* BOTN DEBAJO CON TU COMPONENTE */}
        <motion.div
          className="final-section__button"
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.4 }}
          transition={{ duration: 0.6, ease: "easeOut", delay: 0.3 }}
          whileHover={{ scale: 1.03 }}
          whileTap={{ scale: 0.97 }}
        >
          <HoverBorderGradient as="a" href="#contacto" className="hbg--light">
            Agendar Cita
          </HoverBorderGradient>
        </motion.div>
      </motion.div>
    </section>
  );
};

export default FinalSection;
